#header {
    display: flex;
  }
  
  .nav-bar {
    position: fixed;
    top: 0px;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    background-color: #eee;
    overflow: hidden;
  }
  
  @media only screen and (max-width:600px){
  #nav-bar ul {
    display: flex;
  }
  #nav-bar ul li {
    padding: 1rem 2rem;
  }
  #nav-bar ul li a {
    color: #333;
    font-size: 1.2rem;
  }     }

  .logo {
    width: 300px;
    cursor: pointer;
  }
  
  .nav-link {
      float: left;
      display: block;
      color: #000000;
      text-align: center;
      padding: 14px 16px;
      text-decoration: none;
      font-size: 17px;
  }

  @media only screen and (max-width: 500px) {
    .nav-bar {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 1rem 1rem;
      margin-left: 12rem;
    }

    .nav-bar .logo{
      margin-left: 3rem;
    }
  
    .nav-bar ul .nav-link {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 1rem 0.5rem ;
    }
  }

 