#features {
    padding: 1rem;
    margin-top: 3rem;
    margin-bottom: 3rem;
    display: flex;
    flex-direction: column;
  }
  
  #features .feature {
    display: flex;
    justify-self: center;
    align-items: center;
  }
  
  #features .feature i {
    color: #ff8c00;
  }
  
  #features .feature div {
    padding: 1rem;
    margin-left: 3rem;
  }
  
  #features .feature div h2 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
  }

  @media only screen and (max-width: 500px) {
    #features .feature {
      flex-direction: column;
    }
  
    #features .feature  div {
      padding: 1rem;
      margin-left: 9rem;
      text-align: center;
      margin-bottom: 2rem;
    }

    #features .feature i {
      padding: 0.5rem;
      margin-left: 9rem;
      text-align: center;
      margin-bottom: 0.5rem;
    }
  }

 

 