#pricing{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 3rem 0.2rem;
}

.price-box{
    border: solid #333 2px;
    border-radius: 2px;
    text-align: center;
}

.price-box:first-child {
    margin-right: 1.2rem;
  }
  
  .price-box:last-child {
    margin-left: 1.2rem;
  }

  .price-box h3 {
    background-color: #ddd;
    font-size: 1.05rem;
    padding: 1rem;
  }
  
  .price-box h2 {
    padding: 1rem;
  }

  .price-box p {
    padding: 0 4rem 1rem;
  }
  
  .price-box .btn {
    display: inline-block;
    margin: 1rem;
  }

  @media only screen and (max-width: 500px){
    #pricing {
        flex-direction: column;
        margin-left: 9rem;
      }
    
      #pricing .price-box {
        margin-bottom: 2rem;
        justify-content: center;
        align-items: center;
      }
    
      .price-box:first-child {
        margin-right: 0;
      }
    
      .price-box:last-child {
        margin-left: 0;
      }
  }