.container {
  max-width: 960px;
  margin: 2rem auto 0;
  overflow: hidden;
}

.btn {
  display: inline-block;
  padding: 0.4rem 1.5rem;
  color: #333;
  text-transform: uppercase;
  font-weight: bold;
  background-color: rgb(250, 250, 37);
  cursor: pointer;
}

.btn:hover {
  background-color: #f1c40f;
}

.message-box {
  margin: 1.5rem;
  padding: 0.8rem;
  text-align: center;
}

.message-box h1 {
  font-size: 1.7rem;
  margin-bottom: 1rem;
}

.message-box input {
  display: block;
  margin: auto;
  width: 30%;
  padding: 0.4rem;
  margin-bottom: 1rem;
}

@media only screen and (max-width: 500px) {
  .container {
    padding: 1rem;
    margin-left: 7rem;
    text-align: center;
    margin-bottom: 2rem;
  }

  .container .message-box h1{
    margin-top: 2rem;
    text-align: center;
    justify-content: center;
    padding: 1rem;
  }

  .container .message-box .box{
    width: 200px;
  }

  .container .message-box .btn{
    width: 200px;
    background-color: rgb(250, 250, 37);
  }

  .container .message-box .btn:hover{
    background-color: #f1c40f;
  }
}
