* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
  }
  
  body {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    line-height: 1.5;
    background-color: #eee;
  }
  
  ul {
    list-style: none;
  }
  
  a {
    text-decoration: none;
  }

  